.main {
    position: relative;
    z-index: -1;
}

form {
    display: flex;
    flex-direction: column;
    width: 400px;
    position: absolute;
    top: 30%;
    left: 60%;
    flex-wrap: wrap;
}

label {
    font-size: 12px;
    letter-spacing: 1px;
    font-weight: bold;
    display: inline-block;
    margin-bottom: 6px;
    margin-top: 20px;
}

input {
    box-sizing: border-box;
    padding: 12px 10px;
    border-radius: 5px;
    border: 1px grey solid;
    outline: none;
    
}


.date input {
    width: 40%;
    margin-right:15px ;
}

.cvc {
    display: flex;
    flex-direction: column;
}

.align {
    display: flex;
    justify-content: space-between;
}

button {
    box-sizing: border-box;
    padding: 12px 10px;
    background-color: hsl(278, 68%, 11%);
    color: white;
    border: none;
    border-radius: 5px;
    margin-top: 15px;
    cursor: pointer;
}


.front {
    background-image: url(../../images/bg-card-front.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 450px;
    padding: 20px;
    box-sizing: border-box;
    color: white;
    border-radius: 10px;
    position: absolute;
    top: 15%;
    left: 10%;
}

.front h1 {
    margin-top: 60px;
    letter-spacing: 2px;
}

.details {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.back {
    
    background-repeat: no-repeat;
    background-size: cover;
    width: 450px;
    padding: 20px;
    box-sizing: border-box;
    color: white;
    border-radius: 10px;
    position: absolute;
    top: 50%;
    left: 15%;
}

.error-txt {
    color: red;
    font-size: 12px;
    margin-top: 2px;
}

.error-input {
    border: 1px solid red;
    color: red;
}


